<template>
  <div class="">

    <div class="form-box tab-cont form-box2">
      <el-form ref="formAdd" :model="formAdd" :rules="rulesAdd" label-width="120px">
        <el-form-item label="选择证书" prop="cert_name">
          <!--<el-input class="diainp" type="text" v-model="formAdd.cert_name" placeholder="请输入证书名称"></el-input>-->
          <el-select v-model="formAdd.cert_name"  :multiple=false class="diainp" placeholder="请选择" @change="changeCert">
            <el-option
              v-for="item in certs"
              :key="item.cert_id"
              :value="item.cert_name">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="颁发机构" prop="authority">
          <el-input class="diainp" type="text" v-model="formAdd.authority" placeholder="请输入颁发机构"></el-input>
        </el-form-item>
        <el-form-item label="证书价格" prop="price">
          <el-input-number v-model="formAdd.price" controls-position="right" class="diainp diainp4"></el-input-number>
          <a>&nbsp;元</a>
        </el-form-item>
        <el-form-item label="申请状态" prop="status" style="display: none;">
          <el-select v-model="formAdd.status" class="diainp" placeholder="请选择" @change="changeStatus">
            <el-option
              v-for="item in sqStatus"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="证书模板" prop="operator">
          <div class="curimg-cont" v-show="curImgUrl != ''" @click="clickImg">
            <img :src="curImgUrl" alt="">
          </div>

          <el-upload v-show="showUnload"
           class="avatar-uploader"
           :class="{disabled:uploadDisabled}"
           :action="uploadUrl"
           :data="imgdata"
           :show-file-list="false"
           list-type="picture-card"
           :multiple='false'
           :file-list="fileList"
           name="image"
           :beforeUpload="beforeUpload"
           :on-preview="handlePictureCardPreview"
           :on-remove="handleRemove"
           :on-change="handleChange"
           :on-success="handleSuccess">
            <img v-if="fileImageUrl" :src="fileImageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <!--<el-dialog :visible.sync="dialogVisible">-->
            <!--<img width="100%" :src="dialogImageUrl" alt="">-->
          <!--</el-dialog>-->

          <!--<el-button size="small" type="primary" @click="saveFile(formImg)">点击上传文件</el-button>-->
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.image_design.name.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <span class="inp-span">字号</span>
            <el-input-number class="diainp3" controls-position="right" :min="8" v-model="formAdd.image_design.name.font_size"></el-input-number>
          </div>
          <div>
            <span class="inp-span">X</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.name.X"></el-input-number>
            <span class="inp-span">Y</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.name.Y"></el-input-number>
          </div>
          <div>
            <span class="inp-span">位置</span>
            <el-checkbox @change="changeNamePosition">居中</el-checkbox>
          </div>
        </el-form-item>
        <el-form-item label="拼音" prop="english_name">
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.image_design.english_name.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <span class="inp-span">字号</span>
            <el-input-number class="diainp3" controls-position="right" :min="8" v-model="formAdd.image_design.english_name.font_size"></el-input-number>
          </div>
          <div>
            <span class="inp-span">X</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.english_name.X"></el-input-number>
            <span class="inp-span">Y</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.english_name.Y"></el-input-number>
          </div>
          <div>
            <span class="inp-span">位置</span>
            <el-checkbox @change="changeEnglishNamePosition">居中</el-checkbox>
          </div>
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.image_design.personal_id.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <span class="inp-span">字号</span>
            <el-input-number class="diainp3" controls-position="right" :min="8" v-model="formAdd.image_design.personal_id.font_size"></el-input-number>
          </div>
          <div>
            <span class="inp-span">X</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.personal_id.X"></el-input-number>
            <span class="inp-span">Y</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.personal_id.Y"></el-input-number>
          </div>
        </el-form-item>
        <el-form-item label="证书编号" prop="serial_number">
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.image_design.serial_number.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <span class="inp-span">字号</span>
            <el-input-number class="diainp3" controls-position="right" :min="8" v-model="formAdd.image_design.serial_number.font_size"></el-input-number>
          </div>
          <div>
            <span class="inp-span">X</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.serial_number.X"></el-input-number>
            <span class="inp-span">Y</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.serial_number.Y"></el-input-number>
          </div>
        </el-form-item>
        <el-form-item label="发证日期" prop="image_design">
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.image_design.date.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <span class="inp-span">字号</span>
            <el-input-number class="diainp3" controls-position="right" :min="8" v-model="formAdd.image_design.date.font_size"></el-input-number>
          </div>
          <div>
            <span class="inp-span">X</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.date.X"></el-input-number>
            <span class="inp-span">Y</span>
            <el-input-number class="diainp3" controls-position="right" v-model="formAdd.image_design.date.Y"></el-input-number>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" :disabled="formAdd.image_design.img_name ==''" @click="saveForm('formAdd')">保存</el-button>
          <el-button type="primary" :disabled="formAdd.image_design.img_name ==''" @click="savePreview('formAdd')">预览</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="img-cont">
      <a class="imgbig">
        <img :src="testUrl" alt="">
      </a>
    </div>

  </div>
</template>

<script>
  import Qs from 'qs'
  import store from '../store'
  export default {
    data () {
      return {
        certs:[],
        curImgUrl:'',
        showUnload:true,
        sqStatus:['开放','关闭'],
        fontse:[],
        formAdd: {
          cert_id:'',
          cert_name: '',
          authority:'',
          price:'',
          status:'enabled',
          image_design:{
            img_name:'',
            date:{
              font:'',
              font_size:12,
              X:0,
              Y:0,
            },
            name:{
              font:'',
              font_size:12,
              X:0,
              Y:0,
              text_align:'left'

            },
            english_name:{
              font:'ddd',
              font_size:12,
              X:0,
              Y:0,
              text_align:'left'
            },
            personal_id:{
              font:'ddd',
              font_size:12,
              X:0,
              Y:0
            },
            serial_number:{
              font:'ddd',
              font_size:12,
              X:0,
              Y:0
            }
          }
        },
        rulesAdd: {
          cert_name: [
            {required: true, message: '请输入证书名称', trigger: 'blur'},
          ],
          authority: [
            {required: true, message: '请输入颁发机构', trigger: 'blur'}
          ],
          price: [
            {required: true, message: '请输入证书价格', trigger: 'blur'},
            {validator: this.validatePrice, trigger: 'blur'}
          ],
          font_sizeeee: [
            {required: true, message: '请输入字号', trigger: 'blur'}
          ]
        },

        formImg:{

        },
        fileList:[],
        imageUrl:'',
        uploadUrl:this.baseUrl+'/api/v1/admin/images',
        dialogImageUrl: '',
        dialogVisible: false,
        getImgUrl:'',
        testUrl:'',

        imgdata:{
          'Authorization':'Bearer '+ localStorage.token
        },
        fileImageUrl:''

      }
    },
    computed: {
      uploadDisabled:function() {
        return this.fileList.length >0
      },

    },
    mounted(){
      store.dispatch('loadSiderbarData');
    },
    created: function(){
      this.getFont();
      this.getCerList();
    },
    methods:{
      getCerList: function(){
        let self = this;
        self.$axios({
          type:'get',
          url:self.baseUrl+'/api/v1/admin/certs'
        }).then(res =>{
          //console.log(res.data);
          self.certs = res.data.data;
        })

      },
      getFont: function(){
        let self = this;
        self.$axios.get(self.baseUrl+'/api/v1/admin/fonts').then(function(res){
          if(res.data.success){
            let fonts = res.data.data;
            self.fontse = fonts;
            if(fonts.length > 0){
              self.formAdd.image_design.date.font = fonts[0];
              self.formAdd.image_design.name.font = fonts[0];
              self.formAdd.image_design.english_name.font = fonts[0];
              self.formAdd.image_design.personal_id.font = fonts[0];
              self.formAdd.image_design.serial_number.font = fonts[0];
            }
          }else{
            self.$message.error(res.data.msg);
          }
        })
      },
      changeNamePosition(val){
        if(val){
          this.formAdd.image_design.name.text_align = 'center';
        }else{
          this.formAdd.image_design.name.text_align = 'left';
        }
      },
      changeEnglishNamePosition(val){
        if(val){
          this.formAdd.image_design.english_name.text_align = 'center';
        }else{
          this.formAdd.image_design.english_name.text_align = 'left';
        }
      },
      changeFont:function(){

      },
      clickImg(){
        let self = this;
        self.curImgUrl = '';
        self.showUnload = true;


      },
      beforeSave(){
        let self = this;
        self.formAdd.price = Number(self.formAdd.price);
        // self.formAdd.image_design.date.font_size = Number(self.formAdd.image_design.date.font_size);
        // self.formAdd.image_design.name.font_size = Number(self.formAdd.image_design.name.font_size);
        // self.formAdd.image_design.english_name.font_size = Number(self.formAdd.image_design.english_name.font_size);
        // self.formAdd.image_design.personal_id.font_size = Number(self.formAdd.image_design.personal_id.font_size);
        // self.formAdd.image_design.serial_number.font_size = Number(self.formAdd.image_design.serial_number.font_size);
        //
        // self.formAdd.image_design.date.x = Number(self.formAdd.image_design.date.x);
        // self.formAdd.image_design.name.x = Number(self.formAdd.image_design.name.x);
        // self.formAdd.image_design.english_name.x = Number(self.formAdd.image_design.english_name.x);
        // self.formAdd.image_design.personal_id.x = Number(self.formAdd.image_design.personal_id.x);
        // self.formAdd.image_design.serial_number.x = Number(self.formAdd.image_design.serial_number.x);
        //
        // self.formAdd.image_design.date.y = Number(self.formAdd.image_design.date.y);
        // self.formAdd.image_design.name.y = Number(self.formAdd.image_design.name.y);
        // self.formAdd.image_design.english_name.y = Number(self.formAdd.image_design.english_name.y);
        // self.formAdd.image_design.personal_id.y = Number(self.formAdd.image_design.personal_id.y);
        // self.formAdd.image_design.serial_number.y = Number(self.formAdd.image_design.serial_number.y);
      },
      savePreview: function(formName){
        const self = this;
        self.$refs[formName].validate(function(valid){
          if (valid) {
            self.beforeSave();
            let param = self.formAdd;
            self.$axios.post(self.baseUrl + '/api/v1/admin/images/certs',param).then(function(res){
              if(res.data.success){
                self.testUrl = res.data.data.imageURL;
                //self.$message({message:'图片生成成功',type:'success'});
              }else{
                self.$message.error(res.data.msg);
              }

            },function(err){
              console.log(err);
            })


          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      saveForm: function(formName){
        const self = this;
        self.$refs[formName].validate(function(valid){
          if (valid) {
            self.beforeSave();
            let params = self.formAdd;
            self.$axios.put(self.baseUrl + '/api/v1/admin/certs',params).then(function(res){
              if(res.data.success){
                self.$message({message:'操作成功',type:'success'});
                store.dispatch('loadSiderbarData')
              }else{
                self.$message.error(res.data.msg);
              }

            },function(err){
              self.$message.error(err.msg);
            })


          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      changeCert(val){
        let self = this;
        let arr = self.certs;
        for(let i in arr){
          if(arr[i].cert_name == val){
            let itemData = arr[i];
            self.formAdd.image_design = itemData.image_design;

            self.formAdd.authority = arr[i].authority;
            self.formAdd.price = arr[i].price;
            self.curImgUrl = self.baseUrl + '/api/' + arr[i].image_design.img_name;
            self.formAdd.image_design.img_name = arr[i].image_design.img_name
            self.showUnload = false;
            self.formAdd.cert_id = arr[i].cert_id;

          }
        }

      },
      validatePrice(rule, value, callback){
        let self = this;
        let reg = /^(0|[1-9]\d*)$/;
        if(!reg.test(value)){
          callback(new  Error('输入必须是非负整数'));
        }else{
          callback();
        }
      },
      changeStatus: function(){

      },
      handleRemove(file, fileList) {
        //console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        // this.dialogVisible = true;
      },
      handleChange: function(file,fileList){

      },
      handleSuccess: function(response,file,fileList){
        let self = this;
        if(response.success){
          // self.getImgUrl = response.data.data.imageURL;
          self.formAdd.image_design.img_name = response.data.imageSavePath;
          self.testUrl = response.data.imageURL;
          self.fileImageUrl = URL.createObjectURL(file.raw);
          self.$message({message:'图片上传成功',type:'success'});
        }else{
          self.$message.error(response.data.msg)
        }

      },
      beforeUpload: function(file,fileName){
        const isJPG = file.type === 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传证书模板只能是 png 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传证书模板大小不能超过 2MB!');
        }

        // let fd = new FormData()
        // fd.append('image', file, fileName)
        // this.$axios.post(this.uploadUrl, fd,{
        //
        //   headers: {
        //     'Authorization': 'Bearer '+localStorage.token
        //   }
        // })

        return isJPG && isLt2M;
      },


    }

  }
</script>
<style scoped>
  .curimg-cont{
    display: inline-block;
    width:218px;
    height:218px;
  }
  .curimg-cont img{
    width:100%;
    height:100%;
  }
  .diainp{width:274px;}
  .diainp2,.diainp3{width:80px;}
  .diainp>>>.el-input__inner{
    padding:0 0 0 15px;
  }
  .diainp2>>>.el-input__inner{
    padding:0 20px 0 6px;
  }
  .diainp3>>>.el-input__inner{
    padding:0 46px 0 6px;
  }
  .diainp4>>>.el-input__inner{
    text-align: left;
  }
  .inp-span{display:inline-block;width:28px;margin:0 5px 0 18px;text-align: right;color:#999;}
  .small-form-item{margin-bottom:8px;}
  .form-box2{float:left;width:45%;}
  .img-cont{float:left;width:54%;text-align: center;}
  .avatar-uploader>>>.el-upload--picture-card{
    width:220px;
    height:220px;
    line-height: 220px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 60px;
    color: #8c939d;
    width: 218px;
    height: 218px;
    line-height: 218px;
    text-align: center;
  }
  .avatar {
    width: 218px;
    height: 218px;
    display: block;
  }

</style>
<style>
  .disabled .el-upload--picture-card {
    display: none;
  }
</style>



